export default {
	data() {
		return {
			userName: '请登录',
			userAvatar: '../../../static/picture/userAvatar.png',
			columnData: [{
				src: 'wdct',
				title: '我的错题'
			}, {
				src: 'plfw',
				title: '我的陪练'
			}, {
				src: 'wdcj',
				title: '我的成绩'
			}, {
				src: 'qhtk',
				title: '切换题库'
			}, ],
			setData: ['我的发表', '我的订单', '题库设置', '收藏', '浏览记录', '设置'],
			data:''
		}
	},
	onLoad: function() {
		console.log('onLoad');
	},
	onShow: function() {
		if (uni.getStorageSync('logon_status') == 1) {
			console.log()
			uni.request({
				url: this.$Url + '/api/v1/get/user', //仅为示例，并非真实接口地址。
				method: 'POST',
				data: {
					token: uni.getStorageSync('token')
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: (res) => {
					if (res.data.code == 200) {
						uni.setStorageSync('userData', res.data.data);
						this.userName = res.data.data.username
						this.userAvatar = res.data.data.avatar == null ? '../../../static/picture/userAvatar.png' : this.$Url+'/'+res.data.data.avatar
					} else {
						uni.showToast({
							icon: 'none',
							title: '网络不给力，请稍后重试',
							duration: 1000
						});
					}
				}
			});

		}else{
			this.userName = '请登录'
			this.userAvatar = '../../../static/picture/userAvatar.png'
		}
	},
	methods: {
		tapLogin: function(e) {
			if (uni.getStorageSync('logon_status') == 0) {
				uni.navigateTo({
					url: '../../login/login?form=1'
				});
			}
		},
		tapNav: function(e) {
			let idx = e.currentTarget.dataset.idx
			if (uni.getStorageSync('logon_status') == 1) {
				if (idx == 0) {
					uni.navigateTo({
						url: '../../my/My-mistake/My-mistake'
					});
				} else if (idx == 1) {
					uni.navigateTo({
						url: '../../my/my-accompany/my-accompany'
					});
				} else if (idx == 2) {
					uni.navigateTo({
						url: '../../my/My-grades/My-grades'
					});
				} else if (idx == 3) {
					uni.navigateTo({
						url: '../../my/Item-bank-selection/Item-bank-selection'
					});
				}
			} else {
				uni.navigateTo({
					url: '../../login/login?form=1'
				});
			}
		},
		tapJump: function(e) {
			let idx = e.currentTarget.dataset.idx
			if (uni.getStorageSync('logon_status') == 1) {
				if (idx == 0) {
					uni.navigateTo({
						url: '../../my/My-publication/My-publication'
					});
				} else if (idx == 1) {
					uni.navigateTo({
						url: '../../my/My-order/My-order'
					});
				}else if (idx == 3) {
					uni.navigateTo({
						url: '../../my/My-collection/My-collection'
					});
				}else if (idx == 4) {
					uni.navigateTo({
						url: '../../my/Browse-record/Browse-record'
					});
				}else if(idx == 5) {
					uni.navigateTo({
						url: '../../my/setting/setting'
					});
				}
				
			} else {
				uni.navigateTo({
					url: '../../login/login?form=1'
				});
			}
		}
	}
}
